import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

// Material UI Components
import { Box, Paper, Toolbar, IconButton, Divider, Button, Dialog, DialogTitle, DialogContent, DialogActions, Tooltip, TextField } from '@material-ui/core';

// Icons
import UndoIcon from '@material-ui/icons/Undo';
import RedoIcon from '@material-ui/icons/Redo';
import InsertPhotoIcon from '@material-ui/icons/InsertPhoto';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';

const useStyles = makeStyles(theme => ({
    Toolbar: {
        width: "100%",
        minHeight: "56px",
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    Divider: {
        alignSelf: 'stretch',
        height: 'auto',
        margin: theme.spacing(1, 3)
    },
}));

export default function StaticToolbar( {editMode, onClickUndo, onClickRedo, onClickToggleImageUrl, onClickClearEditor} ) {
    const Style = useStyles();
    const [hidden, setHidden] = React.useState(true);
    
    React.useEffect(() => {
        if(editMode === true){
            setHidden(false);
        }else{
            setTimeout(() => {
                setHidden(true);
            }, 750)
        }
    }, [hidden, editMode, setHidden])

    // Dialog
    const [open, setOpen] = React.useState(false);
    const [url, setURL] = React.useState('');
    // const [urlValid, setUrlValid] = React.useState('');

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleURL = () => {
        handleClose()
        onClickToggleImageUrl(url)
    }

    return (
        <React.Fragment>
            <Box mb={2} className={editMode === true ? 'animated fadeIn' : 'animated fadeOut'} hidden={hidden}>
                <Paper variant="outlined" className={Style.Paper}>
                    <Toolbar className={Style.Toolbar}>
                        <Tooltip title="UNDO" placement="bottom">
                            <IconButton aria-label="Undo" color="inherit" edge="start" onClick={() => onClickUndo()}>
                                <UndoIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="REDO" placement="bottom">
                            <IconButton aria-label="Redo" color="inherit" edge="end" onClick={() => onClickRedo()}>
                                <RedoIcon />
                            </IconButton>
                        </Tooltip>
                        <Divider className={Style.Divider} orientation="vertical" />
                        <Tooltip title="INSERT IMAGE" placement="bottom">
                            <IconButton aria-label="Insert Image" color="inherit" edge="start" onClick={() => handleClickOpen()} >
                                <InsertPhotoIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="INSERT CANVAS" placement="bottom">
                            <IconButton aria-label="Insert Canvas" color="inherit" edge="end" >
                                <CheckBoxOutlineBlankIcon />
                            </IconButton>
                        </Tooltip>
                        <Divider className={Style.Divider} orientation="vertical" />
                        <Button aria-label="Clear Editor" color="secondary" variant="outlined" onClick={() => onClickClearEditor()}>Clear</Button>
                    </Toolbar>
                </Paper>
            </Box>
            <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alertTitle"
        >
            <DialogTitle id="alertTitle">Enter URL</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    id="url"
                    label="URL"
                    placeholder="www.google.com"
                    type="text"
                    onChange={(event) => setURL(event.target.value)}
                    variant="outlined"
                    required
                    fullWidth
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="secondary">
                    Cancel
                </Button>
                <Button disabled={url === "" ? true : false} onClick={handleURL} color="primary">
                    Submit
                </Button>
            </DialogActions>
        </Dialog>
    </React.Fragment>
)}