import React from 'react';
import Editor, {composeDecorators} from 'draft-js-plugins-editor';
import { EditorState, RichUtils, convertToRaw, Modifier, CompositeDecorator, convertFromRaw, AtomicBlockUtils } from 'draft-js';
import CssBaseline from '@material-ui/core/CssBaseline';

// Draft JS Plugins
import createImagePlugin from 'draft-js-image-plugin';
import createAlignmentPlugin from 'draft-js-alignment-plugin';
import createFocusPlugin from 'draft-js-focus-plugin';
import createResizeablePlugin from 'draft-js-resizeable-plugin';

// Components
import Header from '../components/Header/Header';
import InlineToolbar from '../components/InlineToolbar/InlineToolbar';
import StaticToolbar from '../components/StaticToolbar/StaticToolbar';

// Material UI Components
import { ThemeProvider, Container, Box } from '@material-ui/core';

// Style sheet
import { createMuiTheme } from '@material-ui/core/styles';
import 'draft-js/dist/Draft.css'
import 'draft-js-focus-plugin/lib/plugin.css';
import 'draft-js-image-plugin/lib/plugin.css';
import 'draft-js-alignment-plugin/lib/plugin.css';
import './App.css';

// Material UI Custom Theme
const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#2980B9',
    },
    secondary: {
      main: '#C0392B'
    },
    success: {
      main: '#27AE60',
    }
  },
});

// Draft JS Plugins Instance
const focusPlugin = createFocusPlugin();
const resizeablePlugin = createResizeablePlugin();
const alignmentPlugin = createAlignmentPlugin();
const { AlignmentTool } = alignmentPlugin;

// Draft JS Plugins Decorator
const decorator = composeDecorators(
  resizeablePlugin.decorator,
  alignmentPlugin.decorator,
  focusPlugin.decorator,
);
const imagePlugin = createImagePlugin({ decorator });

const plugins = [
  focusPlugin,
  alignmentPlugin,
  resizeablePlugin,
  imagePlugin
];

export default class App extends React.Component{
  constructor(props){
    super(props);
    
    this.decorator = new CompositeDecorator([
      {
        strategy: findLinkEntities,
        component: Link,
      },
    ]);

    this.state = {
      editorState: EditorState.createEmpty(this.decorator),
      currentNote: null,
      editMode: false,
      fontSize: '14',
      fontColor: 'INHERIT',
      highlighterColor: 'TRANSPARENT',
      notes: [],
    }
  }

  componentDidMount(){
    // Will set a variables with the value of the local storage 
    const setNotes = JSON.parse(localStorage.getItem("Notes") || "[]");

    this.setState({
      notes: setNotes,
    })
  }

  // This method will handle the Editor whenever something changes
  handleEditorChange = (editorState) => {
    // console.log(convertToRaw(editorState.getCurrentContent()));

    this.setState({
      editorState: editorState,
    })
  };

  handleEditorFocus = () => {
    this.editor.focus();
  };

  // This method will handle whenever the user presses the tab key
  onEditorTab = (event) => {
    event.preventDefault();

    const tabCharacter = "    ";
    
    const editorState = this.state.editorState;
    const newContentState = Modifier.replaceText(
      editorState.getCurrentContent(),
      editorState.getSelection(),
      tabCharacter
    );

    this.setState({ 
      editorState: EditorState.push(editorState, newContentState, 'insert-characters')
    });
  }

  // This method will set the editor to edit mode (Not read only) 
  handleClickEditMode = () => {
    this.handleCursorSetAtEnd(this.state.editorState);
    
    this.setState(state => ({
      editMode: !state.editMode,
    }))
  }

  // This method will toggle the a block style
  handleToggleBlockStyle = (block) => {
    // const checkStyle = (style === 'UL' ? 'unordered-list-item' : style === 'OL' ? 'ordered-list-item' : style.toUpperCase() )
    this.handleEditorChange(RichUtils.toggleBlockType(this.state.editorState, block))
  }
  
  // This method will either toggle a style or remove it then apply the new one if the style is any of the values from the array
  handleToggleStyle = (style) => {
    const checkStylesArray = ['14', '16', '24', '32', '40', '64', '96', "INHERIT", "#FAFAFA", "#BDC3C7", "#C0392B", "#F1C40F", "#F39C12", "#8E44AD", "#2980B9", "#27AE60", "TRANSPARENT", "#34495E", "#95A5A6", "#E74C3C", "#E67E22","#9B59B6", "#3498DB", "#2ECC71", "#1ABC9C"];

    if(checkStylesArray.includes(style)){
      this.handleToggleRemoveStyle(style);
    }else{
      this.handleEditorChange(RichUtils.toggleInlineStyle(this.state.editorState, style))
    }
  }

  // This method will remove the current style from the selected text 
  handleToggleRemoveStyle = (style) => {
    const currentEditorState = this.state.editorState;
    const fontSize = ['14', '16', '24', '32', '40', '64', '96'];
    const fontColor = ["INHERIT", "#FAFAFA", "#BDC3C7", "#C0392B", "#F1C40F", "#F39C12", "#8E44AD", "#2980B9", "#27AE60"];
    const highlighterColor = ["TRANSPARENT", "#34495E", "#95A5A6", "#E74C3C", "#E67E22","#9B59B6", "#3498DB", "#2ECC71", "#1ABC9C"];
    let previousStyle;

    if(fontSize.includes(style)){
      previousStyle = this.state.fontSize;

      this.setState({
        fontSize: style
      })
    }else if(fontColor.includes(style)){
      previousStyle = this.state.fontColor;

      this.setState({
        fontColor: style
      })
    }else if(highlighterColor.includes(style)){
      previousStyle = this.state.highlighterColor;

      this.setState({
        highlighterColor: style
      })
    }

    const selectionState = currentEditorState.getSelection();
    const contentState = currentEditorState.getCurrentContent();

    const removePreviousStyle = Modifier.removeInlineStyle(
      contentState,
      selectionState,
      previousStyle
    );
      
    const removePreviousStyleBlock = removePreviousStyle.getBlockMap();
    
    const newInlineStyle = Modifier.mergeBlockData(removePreviousStyle, selectionState, removePreviousStyleBlock);
    const newEditorState = EditorState.push(currentEditorState, removePreviousStyle, newInlineStyle);

    this.handleToggleApplyStyle(newEditorState, style);
  }

  // This method will apply the style chosen to the selected text
  handleToggleApplyStyle = (editorState, style) => {
    const newEditorState = editorState;

    const selectionState = newEditorState.getSelection();
    const contentState = newEditorState.getCurrentContent();

    const applyNewStyle = Modifier.applyInlineStyle(
      contentState,
      selectionState,
      style
    );
      
    const applyNewStyleBlock = applyNewStyle.getBlockMap();
    
    const newInlineStyle = Modifier.mergeBlockData(applyNewStyle, selectionState, applyNewStyleBlock);
    const updateEditorState = EditorState.push(newEditorState, applyNewStyle, newInlineStyle);

    this.setState({
      editorState: updateEditorState,
    });
  }

  // This method will set the cursor at the end of the editor
  handleCursorSetAtEnd = (editorState) => {
    const currentEditorState = editorState === null ? this.state.editorState : editorState;

    this.setState({
      editorState: EditorState.moveFocusToEnd(currentEditorState)
    })
  }

  // This method will clear the editor
  handleClickClearEditor = () => {
    const newEditorState = EditorState.createEmpty();

    const selectionState = newEditorState.getSelection();
    const contentState = newEditorState.getCurrentContent();

    const contentBlock = contentState.getBlockMap();
  
    const newBlock = Modifier.setBlockData(contentState, selectionState, contentBlock);
    const updateEditorState = EditorState.push(newEditorState, contentState, newBlock);

    this.setState({
      editorState: updateEditorState,
    })
  }

  // This method will set the editor back to view-mode and will not save the changes that the user has done.
  onClickCancelEditor = () => {
    this.setState(state => ({
      editMode: !state.editMode,
    }))
  }

  // This method will remove the last style inserted into the selected text
  handleClickRemoveLastFormat = (key) => {
    const editorState = this.state.editorState;
    const selectionState = editorState.getSelection();
  
    const contentState = editorState.getCurrentContent();

    const editorRaw = convertToRaw(editorState.getCurrentContent());
    const editorRawBlock = editorRaw.blocks.find(item => item.key === key);

    const inlineStyleArray = editorRawBlock.inlineStyleRanges;

    inlineStyleArray.forEach(item => {
      const removeStyle = Modifier.removeInlineStyle(
        contentState,
        selectionState,
        item.style
      );

      const removeStyleBlock = removeStyle.getBlockMap();
      
      const newInlineStyle = Modifier.setBlockData(removeStyle, selectionState, removeStyleBlock);

      const newEditorState = EditorState.push(editorState, removeStyle, newInlineStyle);

      this.setState({
        editorState: newEditorState,
      });
    })
  }
  
  // This method will turn the text into a working link
  handleClickToggleLink = (url) => {
    const editorState = this.state.editorState;
    const selectionState = editorState.getSelection();

    const contentState = editorState.getCurrentContent();
    const contentWithEntity = contentState.createEntity('LINK', 'MUTABLE', { url: url });
    const newEditorState = EditorState.push(editorState, contentWithEntity, 'create-entity');
    const entityKey = contentWithEntity.getLastCreatedEntityKey();

    this.handleEditorChange(RichUtils.toggleLink(newEditorState, selectionState, entityKey));
  }

  // This method will set the url entered into an image component inside the editor
  handleClickToggleImageUrl = (url) => {
    const newEditorState = handleInsertImage(this.state.editorState, url);
    this.handleEditorChange(newEditorState)
  }

  // This method will save the editor and title and create with the data a new note
  handleClickSave = (titleParam) => {
    const date = new Date();
    const currentNotes = [...this.state.notes];
    const newCurrentNote = this.state.currentNote;

    const noteData = convertToRaw(this.state.editorState.getCurrentContent());

    const contentBlock = noteData.blocks[0];
    const contentText = contentBlock.text;
    const contentTextArray = contentText.split(" ");

    let title;
    let drawerTitle;
    
    if(titleParam === "New Note" || titleParam === "" ){
      title = contentTextArray[0];

      const titleArray = title.split("");
      const newTitleArray = titleArray.slice(0, 12);
      const newTitle = newTitleArray.join("");
  
      drawerTitle = `${newTitle}...`;
    }else{
      if(titleParam.length > 12){
        const titleArray = titleParam.split("");
        const newTitleArray = titleArray.slice(0, 12);
        const newTitle = newTitleArray.join("");
    
        drawerTitle = `${newTitle}...`;
      }else{
        drawerTitle = titleParam;
      }
      title = titleParam;
    }

    const day = date.getDate()
    const month = (date.getMonth() + 1)
    const year = date.getFullYear()
    

    const fullDate = `${month}/${day}/${year}`;

    let newNote;
    let newNotes = [];

    if(newCurrentNote === null){
      newNote = {
        id: Math.random(),
        title: title,
        titleDrawer: drawerTitle,
        date: fullDate,
        password: "",
        locked: false,
        data: noteData
      }

      newNotes = [...currentNotes];
      newNotes.push(newNote);
    }else{
      newNote = {
        id: newCurrentNote.id,
        title: title,
        titleDrawer: drawerTitle,
        date: fullDate,
        password: newCurrentNote.password,
        locked: newCurrentNote.locked,
        data: noteData
      }

      newNotes = currentNotes.map(item => {
        return item.id === newNote.id ? item = newNote : item
      })
    }

    // Setting the Alarms array into our local storage
    localStorage.setItem("Notes", JSON.stringify(newNotes));

    this.setState(state => ({
      currentNote: newNote,
      editMode: !state.editMode,
      notes: newNotes,
    }))
  }

  handleClickClearNotes = () => {
    // Deleting the Notes item from local storage
    localStorage.removeItem("Notes")

    this.setState({
      notes: []
    })
  }

  handleClickToggleLock = (id, password, type) => {
    // Getting the current state of notes array
    const currentNotes = [...this.state.notes];
    
    const newNotes = currentNotes.map(item => {
      if(item.id === id){
        if(type === "turnOn"){
          item.password = password;
        }else{
          item.password = "";
        }
        item.locked = !item.locked;
      }
      return item
    })

    // Setting the Notes array into our local storage
    localStorage.setItem("Notes", JSON.stringify(newNotes));

    this.setState({
      notes: newNotes
    })
  }
  
  // This method will delete the note from the notes array
  handleClickDeleteNote = (id) => {
    const itemId = id;

    // Getting the current state of notes array
    const currentNotes = [...this.state.notes];

    // Remove the item from the new notes array
    const newNotes = currentNotes.filter(item => {
        return item.id !== itemId
    });

    // Setting the Notes array into our local storage
    localStorage.setItem("Notes", JSON.stringify(newNotes));

    this.setState({
        notes: newNotes,
    })
  }

  handleClickOpenNote = (type, note) => {
    if(type === "New"){
      this.setState(state => ({
        currentNote: null,
        editorState: EditorState.createEmpty(this.decorator),
      }))
    }else{
      const data = convertFromRaw(note.data);

      this.setState({
        currentNote: note,
        editorState: EditorState.createWithContent(data)
      })
    }
  }

  // This method will undo the last edit to the editor
  handleUndo = () => {
    this.handleEditorChange(EditorState.undo(this.state.editorState));
  } 

  // This method will redo the last edit that was undone to the editor
  handleRedo = () => {
    this.handleEditorChange(EditorState.redo(this.state.editorState));
  }
  
  render(){
    return (
      <React.Fragment>
        <CssBaseline />
        <ThemeProvider theme={theme}>
          <Container className='Container' maxWidth='md' disableGutters>
            <Header 
              editMode={this.state.editMode}
              currentNote={this.state.currentNote}
              notes={this.state.notes}
              onClickSave={this.handleClickSave} 
              onClickEditMode={this.handleClickEditMode} 
              onClickCancelEditor={this.onClickCancelEditor}
              onClickClearNotes={this.handleClickClearNotes} 
              onClickToggleLock={this.handleClickToggleLock} 
              onClickDeleteNote={this.handleClickDeleteNote} 
              onClickOpenNote={this.handleClickOpenNote} 
              />
            <Box m={2}>
              <StaticToolbar 
                editMode={this.state.editMode}
                onClickUndo={this.handleUndo}
                onClickRedo={this.handleRedo}
                onClickToggleImageUrl={this.handleClickToggleImageUrl}
                onClickClearEditor={this.handleClickClearEditor}
              />
              <div onClick={() => this.handleEditorFocus}>
                <Editor 
                  customStyleMap={inlineStyles}
                  blockStyleFn={blockStyles}
                  editorState={this.state.editorState}
                  onChange={this.handleEditorChange}
                  onTab={this.onEditorTab}
                  plugins={plugins}
                  ref={(element) => { this.handleEditorFocus = element; }}
                  readOnly={!this.state.editMode}
                  spellCheck={true}
                  placeholder='Enter your thoughts...'
                />
                <AlignmentTool />
              </div>
              <InlineToolbar 
                editorState={this.state.editorState} 
                onClickToggleBlockStyle={this.handleToggleBlockStyle}
                onClickToggleStyle={this.handleToggleStyle}
                handleCursorSetAtEnd={this.handleCursorSetAtEnd}
                onClickRemoveLastFormat={this.handleClickRemoveLastFormat}
                onClickToggleLink={this.handleClickToggleLink}
              />
            </Box>
          </Container>
        </ThemeProvider>
      </React.Fragment>
    );
  }
}

// Custom block styles
function blockStyles(contentBlock) {
  const type = contentBlock.getType();

  // Alignment
  if (type === 'ALIGNLEFT') {
    return 'alignLeft';
  } else if (type === 'ALIGNCENTERED') {
    return 'alignCenter';
  } else if (type === 'ALIGNRIGHT') {
    return 'alignRight';
  }

  // Lists 
  else if (type === 'unordered-list-item') {
    return 'UL';
  } else if (type === 'ordered-list-item') {
    return 'OL';
  }
}

